@import "src/scss/sizes";
@import "src/scss/functions";

.about {
  padding: 160px 0 118px;
  display: flex;
  align-items: center;

  @include for-mx-down {
    padding: 72px 0 118px;
  }

  @include for-hg-down {
    padding: 72px 0 80px;
    background-color: var(--secondary-color);
  }

  &__content {
    display: flex;
    align-items: center;

    @include for-hg-down {
      flex-direction: column;
    }
  }

  &__video-wrapper {
    position: relative;
    flex: 0 0 752px;
    width: 752px;
    height: 462px;
    margin-right: 56px;
    max-width: 50%;

    @include for-lg-down {
      flex: 0 0 508px;
      width: 508px;
      height: 288px;
      margin-right: 64px;
    }

    @include for-hg-down {
      width: 606px;
      flex: 0 0 372px;
      height: 372px;
      margin-right: 0;
      margin-bottom: 48px;
    }

    @include for-md-down {
      width: 450px;
      flex: 0 0 282px;
      height: 282px;
    }

    @include for-mb-down {
      flex: 0 0 186px;
      width: 328px;
      height: 186px;
    }
  }

  &__video {
    position: absolute;
    width: 80%;
    height: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;

    &:not(.show) {
      display: none;
    }
  }

  &__video-preview-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;

    &.hide {
      display: none;
    }

    &:hover {
      .about__video-play {
        transform: scale(1.1);
        filter: drop-shadow(0px 0px 6px #000);
      }
    }
  }

  &__video-preview {
    position: absolute;
    width: 100%;
    height: 100%;
    cursor: pointer;
    object-fit: contain;
  }

  &__video-play {
    position: absolute;
    width: 84px;
    height: 84px;
    cursor: pointer;
    transition: all var(--transition-duration-fast);

    &.hide {
      display: none;
    }
  }

  &__info {
    width: 100%;
    flex: 1 1 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
  }

  &__title {
    font-weight: 500;
    font-size: 48px;
    line-height: 52px;
    color: white-shade-color(92);
    text-shadow: var(--blue-shadow);
    margin-bottom: 24px;
  }

  &__text {
    font-size: 20px;
    line-height: 24px;
    color: white-shade-color(64);

    @include for-lg-down {
      font-size: 16px;
      line-height: 20px;
    }

    p {
      & + & {
        margin-top: 20px;
      }
    }
  }
}
